.container-servicios {
    width: 90%;
    height: 100vh;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .imagen {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40%;
        height: auto;
        .Celular {
            width: 600px;
            height: auto;
        }
        .M-G {
            position: absolute;
            width: 200px;
            height: auto;
            left: 100px;
            animation-name: megusta;
            animation-duration: 5s;
            transition: cubic-bezier(0.455, 0.03, 1.515, 0.955);
            animation-iteration-count: infinite;
            // transition-timing-function: cubic-bezier(0.1, 0.9, 0.2, 0.1);
        }
        .caja-b,
        .caja-f {
            width: 600px;
            height: auto;
        }
        .caja-b {
            animation-name: caja;
            animation-duration: 3s;
            transition: ease;
            animation-iteration-count: infinite;
        }
        .caja-f {
            position: absolute;
        }
    }
    .texto-content {
        width: 40%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        .boton-serv {
            margin-top: 5px;
            width: auto;
            padding: 5px;
            padding-left: 15px;
            padding-right: 15px;
            font-family: "colegas-bold";
            color: white;
            font-size: 20px;
            border-radius: 20px;
            background: none;
            border: 2px white solid;
            transition: 0.3s ease-out;
            outline: none;
            &:hover {
                box-shadow: rgba(0, 0, 0, 0.308) 5px 5px 0;
                transform: translateY(-5px);
            }
        }
        svg {
            width: 500px;
            height: auto;
        }
        p {
            margin-top: 25px;
            color: rgba(255, 255, 255, 0.842);
            font-family: "colegas-regular";
            width: 80%;
        }
    }
}

@keyframes megusta {
    0% {
        transform: translateY(0px) translateX(0px);
    }

    25% {
        transform: translateY(-10px) translateX(5px);
    }

    50% {
        transform: translateY(-10px) translateX(-10px);
    }
    75% {
        transform: translateY(-20px) translateX(5px);
    }
    100% {
        transform: translateY(0px) translateX(0px);
    }
}

.Drop_x0020_Shadow {
    fill: none;
}
.Outer_x0020_Glow_x0020_5_x0020_pt {
    fill: none;
}
.Blue_x0020_Neon {
    fill: none;
    stroke: #8aacda;
    stroke-width: 7;
    stroke-linecap: round;
    stroke-linejoin: round;
}
.Chrome_x0020_Highlight {
    fill: url(#SVGID_1_);
    stroke: #ffffff;
    stroke-width: 0.3629;
    stroke-miterlimit: 1;
}

.st0 {
    fill: #ffffff;
}

@keyframes caja {
    0% {
        transform: translateX(0px);
    }
    50% {
        transform: translateX(10px);
    }
    100% {
        transform: translateX(0px);
    }
}
