@font-face {
    font-family: "colegas-book";

    src: url("assets/font/GT-Sectra-Book.woff");
}

@font-face {
    font-family: "colegas-bold";

    src: url("assets/font/f37ginger-bold-webfont.woff");
}
@font-face {
    font-family: "colegas-regular";

    src: url("assets/font/f37ginger-regular-webfont.woff");
}

.pestana {
    width: 200px;
    height: auto;
    position: fixed;
    // background-color: #dd3a4e;
    transition: 0.5s;
    transition-timing-function: cubic-bezier(0.1, 0.3, 0.2, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    .logo-2 {
        width: 200px;
        margin-top: 30px;
        margin-left: 50px;
        transition: 2s ease-out;
        opacity: 1;
    }
    .logo-1 {
        width: 70px;
        margin-top: 10px;
        margin-left: 10px;
        opacity: 0;
        transition: 2s ease-out;
        display: none;
    }
}

body {
    margin: 0px;
    padding: 0px;
    // background-color:  !important;

    // background-color: #3800fe !important;
    transition: 0.3s ease-out;
    font-family: "colegas-regular";
    // background: #dd3a4e;
    transition: all 500ms;
    will-change: background;
}

html[data-scroll="0"] {
    body {
        background-color: #000 !important;
    }
    .pestana {
        margin-top: 30px !important;
        width: 100%;
        height: 55px;
        .logo-2 {
            opacity: 0;
            display: none;
        }
        .logo-1 {
            display: block;
            opacity: 1;
        }
    }
    .container {
        .inicio {
            background-image: url("./assets/img/fondo-home-01.svg");
            background-size: cover;
            background-repeat: no-repeat;
            .texto-titulo {
                .colegas {
                    opacity: 1;
                }
                p {
                    opacity: 1;
                }
            }
            .estatua {
                opacity: 1 !important;
                .Busto {
                }
            }
        }
        .Que-es {
            opacity: 0;
        }
    }
}
h1 {
    // font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

    font-family: "colegas-bold";
    transition: 0.3s;
    font-size: 42px;
    text-align: center;
    line-height: 1.2em;
    letter-spacing: -0.04em;
    opacity: 1;
}
p {
    font-size: 16px;
    line-height: 1.7em;
    letter-spacing: -0.02em;
    font-family: "colegas-Regular";
}
.container {
    .inicio {
        height: 100vh;
        width: 100%;

        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        .texto-titulo {
            display: flex;
            width: 50%;
            height: 100%;
            justify-content: center;
            align-items: center;
            transform: translateX(-50px);
            flex-direction: column;
            // background-color: #000;
            .colegas {
                width: 500px;
                opacity: 0;
                // position: fixed;
            }
            p {
                margin-left: 22%;
                margin-right: 10%;
                color: rgb(195, 195, 195);
                opacity: 0;
                transition: 1s ease-out;
                // font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
            }
        }
        .estatua {
            width: 50%;
            height: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            opacity: 0;
            transition: 0.5s ease-out;

            .Busto {
                width: 100%;
                position: absolute;
                transition: 0.3s ease-out;
            }
            &:hover {
                .B2 {
                    transform: translateX(0px) translateY(-25px);
                }
                .B3 {
                    transform: translateX(0px) translateY(-50px);
                }
            }
        }
    }
    .Que-es {
        height: 800px;
        position: relative;
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;

        flex-direction: column;

        h1 {
            // font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
            margin-left: 30%;
            margin-right: 30%;
            color: white;
            z-index: 2;
        }
        p {
            color: rgb(235, 235, 235);
            margin-top: 0px;
            text-align: center;
            margin-left: 30%;
            margin-right: 30%;
            z-index: 2;
        }
        .Busto2 {
            background-image: url("assets/img/busto-rojo.png");
            position: absolute;
            width: 400px;
            height: 650px;
            background-size: cover;
            animation-name: spin;
            animation-duration: 5000ms;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
            mix-blend-mode: multiply;
            z-index: 1;
        }
    }
    .Servicios {
        // background-color: #3800fe;
        height: 100vh;
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .container-serc {
            background-color: rgb(45, 0, 77);
            height: 100px;
            width: 100px;
        }
    }
}

@keyframes spin {
    0% {
        transform: translateY(0px) translateX(-10px);
    }

    50% {
        transform: translateY(10px) translateX(5px);
    }
    100% {
        transform: translateY(0px) translateX(-10px);
    }
}
.trabajos-recientes {
    height: 110vh;
    width: 100%;
    display: flex;

    justify-content: center;
    align-items: center;
    h1 {
        position: absolute;
        font-size: 100px;
        color: white;
        line-height: 0.9em;
    }
}
.pre-footer {
    height: 400px;
    width: 100%;
    background-color: whitesmoke;
}
footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: white;
    height: 50px;
    .derechos {
        font-size: 14px;
        font-family: "colegas-regular";
        margin-left: 100px;
    }
    .redes {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 200px;
        height: 100%;
        margin-right: 100px;
        .ico {
            width: 25px;
            height: auto;
        }
    }
}
